import Nav from "../../NavUrl/urlNavigateTo";
import uniPopup from "@/components/uni-popup/uni-popup.vue";
import BaseButton from "components/base-button/base-button";
import moment from "common/js/moment.min.js";
import uParse from "components/plugin/uParse/src/wxParse";
import VocabModal from "components/vocab-modal/vocab-modal";
export default {
    name: "MyDate",
    components: { uniPopup, BaseButton,uParse,VocabModal },
     props: {
        isback: {
            type: Boolean,
            value: ""
        },
    },
    data() {
        return {
            date: ["Mon", "Tues", "Wed", "Thur", "Fri", "Sat", "Sun"],
            // 当天所在的一周的日期
            days: [],
            // 根据数据显示是否有提示
            state: [],
            //默认当天的下标
            selected: Number,
            initSelected: Number,
            //当天日期
            today: Number,
            //任务状态，false显示任务已完成的空状态
            missionState: true,
            // 当天日期 默认格式20191010
            dateTime: "",
            // // 初始当天默认未完成数量
            currentWeekList: Array,
            // 未完成任务的数量
            incomplete: Number,
            // 第一个任务作业或课程   scheduleType  日程类别1作业2课程
            firstTask: Object,
            // 初始第一个任务
            initFirstTask: Object,
            //创建时获取年月日
            years: Number,
            month: Number,
            day: Number,
            // 开始时间 格式20191018
            startTime: "",
            // 结束时间  格式20191018
            endTime: "",
            backgroundColor: "orange",
            curId:'',
            name:'',
            wordType:'',
            mid:'',
        };
    },
    created() {
        this.years = new Date().getFullYear(); //当前年份
        this.month = new Date().getMonth() + 1; //当前月份，返回0-11
        this.day = new Date().getDate(); //今天，返回今天所在的日期
        this.dateTime = `${this.years}${
            this.month < 10 ? "0" + this.month : this.month
        }${this.day < 10 ? "0" + this.day : this.day}`;
        this.getWeekDate(); //初始化获取日期
        // this.getWeekDateState(); //获取一周的数据，显示状态
  },
    onReady() {},
    watch: {
        selected: function(val, oldVal) {
            //初始化会执行，所以加判断，变化才允许执行
            if (!typeof oldVal == "function") {
                // 当天第一个未完成的任务数据在这申请
                this.$http
                    .get(
                        this.$api.homework.allPlan.getSchedule +
                            `?dateTime=${this.dateTime}`,
                        {
                            "Content-Type": "application/json;charset=UTF-8"
                        }
                    )
                    .then(res => {
                        this.firstTask = res.data.firstTask;
                    });
            }
        },
        dateTime: function(val, oldVal) {
            if (oldVal) {
                //执行请求第一条未完成任务
                this.updateFirstTask();
            }
        }
    },
    methods: {
        getWeekDateState() {
            //获取一周的数据，显示状态
            this.$http
                .get(
                    this.$api.homework.allPlan.getAllSchedule +
                        `?startTime=${this.startTime}&endTime=${this.endTime}`,
                    {
                        "Content-Type": "application/json;charset=UTF-8"
                    }
                )
                .then(res => {
                    //储存当周的任务数量情况
                    this.currentWeekList = res.data.dailyList;
                    this.incomplete = this.currentWeekList[
                        this.selected
                    ].incomplete;
                    this.state = []; //状态清空
                    res.data.dailyList.forEach(item => {
                        if (item.incomplete !== 0) {
                            this.state.push(1);
                        } else {
                            this.state.push(0);
                        }
                    });
                    // 判断任务完成是否为空状态
                    if (this.incomplete > 0) {
                        this.missionState = true;
                    } else {
                        this.missionState = false;
                    }
                });
        },
        updateFirstTask() {
            // 当天第一个未完成的任务数据在这申请
            // 第一条未完成任务 返回更新
            this.$http
                .get(
                    `${this.$api.homework.allPlan.getSchedule}?dateTime=${this.dateTime}`,
                    {
                        "Content-Type": "application/json;charset=UTF-8"
                    }
                )
                .then(res => {
                    this.firstTask = res.data.firstTask;
                });
        },
        getWeekDate() {
            //获取当天所在的一周的日期 周一到周日对应是几号
            var new_Date = new Date();
            // var timesStamp = Date.parse("Oct 28, 2019");
            var timesStamp = new_Date.getTime(); //时间戳
            var currenDay = new_Date.getDay(); //当天周几

            var dates = [];
            for (var i = 0; i < 7; i++) {
                dates.push({
                    day: new Date(
                        timesStamp +
                            24 * 60 * 60 * 1000 * (i - ((currenDay + 6) % 7))
                    ).getDate(),
                    month:
                        new Date(
                            timesStamp + 24 * 60 * 60 * 1000 * (i - ((currenDay + 6) % 7))
                        ).getMonth() + 1,
                    years: new Date(
                        timesStamp +
                            24 * 60 * 60 * 1000 * (i - ((currenDay + 6) % 7))
                    ).getFullYear()
                });
            }

            // 获取开始时间 格式20191018
            this.startTime = `${dates[0].years}${
                dates[0].month < 10 ? "0" + dates[0].month : dates[0].month
            }${dates[0].day < 10 ? "0" + dates[0].day : dates[0].day}`;
            // 获取结束时间  格式20191018
            this.endTime = `${dates[6].years}${
                dates[6].month < 10 ? "0" + dates[6].month : dates[6].month
            }${dates[6].day < 10 ? "0" + dates[6].day : dates[6].day}`;

            //当天的日期号数
            this.today = new_Date.getDate();
            //计算当天日期 下标 替换 “今”字
            var num = currenDay - 1;
            if (num < 0) {
                num = 6;
            }
            this.initSelected = num;
            this.selected = num;
            dates[num].day = "今";
            this.days = dates;
        },

        toDoHomework() {
            if (this.firstTask.scheduleType == 1) {
                console.log('this.firstTask',this.firstTask)
                var examItemId = this.firstTask.exams ? this.firstTask.exams.id : "";
                this.curId = examItemId;
                this.name = this.firstTask.title;
                this.mid = this.firstTask.mId;
                var tagId = this.firstTask.exams ? this.firstTask.exams.tagId : "";
                this.wordType = tagId;
                var aeid = this.firstTask.exams ? this.firstTask.exams.aeid : "";
                if(tagId == 413 || tagId == 804){
                    this.$http.get(this.$api.special.getRecite, {
                        examItemID: examItemId
                    },{"Content-Type": "application/json;charset=UTF-8"}).then(res => {
                        if(res.data.isRecite!=0 && !res.data.attr && aeid==0){
                        this.$refs.vocabModal.show();
                    }else{
                        // 作业跳转逻辑
                        Nav.urlNavigateTo(this.firstTask,res.data);
                    }

                });
                }else{
                    // console.log("这是作业");
                    Nav.urlNavigateTo(this.firstTask);
                }
            } else if(this.firstTask.scheduleType == 2){
                // 课程弹窗
                switch(this.firstTask.type){
                        case '0':  //普通实体课
                            this.$refs.popup.open();
                            break;
                        case '1': //直播课
                            if(this.compare(this.firstTask.dateTime) == false){
                                console.log('跳转直播');
                                uni.navigateTo({
                                    url:`${this.$path.live.liveDetailUrl}?appID=${this.firstTask.appId}&id=${this.firstTask.sourceID}`
                                })
                            }
                            break;
                        case '2': //点播课
                            let {vid,title:videoTitle,id}=this.firstTask;
                            uni.navigateTo({
                                    //路由带参数
                                    url:`${this.$path.play.mpWeixin.coursePlayDetailsUrl}?vid=${vid}&courseID=${id}&videoTitle=${videoTitle}`
                            })
                            break;
                }
            }
        },
        showAllplan() {
            //跳转到全部安排
            uni.navigateTo({
                url: this.$path.homework.allPlan.allPlan
            });
            // console.log("查看全部安排")
        },
        // 点击选择其他日期
        getindex(e) {
            this.selected = e.currentTarget.dataset.id;
            this.incomplete = this.currentWeekList[this.selected].incomplete;
            // 判断任务完成是否为空状态
            if (this.incomplete > 0) {
                this.missionState = true;
            } else {
                this.missionState = false;
            }
            // 点击选择不同日期，
            let Day = e.currentTarget.dataset.date;
            let currentItem = e.currentTarget.dataset.item;
            // console.log("日期改变");
            this.dateTime = `${currentItem.years}${currentItem.month}${
                Day < 10 ? "0" + Day : Day
            }`;
            // console.log(this.dateTime);
        },
        // 关闭弹窗
        closePopup() {
            this.$refs.popup.close();
        },
        getButtonText(item) {
            // console.log(item);
            // firstTask.scheduleType==1?'做作业':'查看'
            let text = "";
            if (item.scheduleType == 1) {
                text = "做作业";
            } else if (item.scheduleType == 2) {
                if (item.type == 0||item.type == 2) {
                    //实体课程
                    text = "查看";
                } else if (item.type == 1) {
                    if (this.compare(item.dateTime) == true) {
                        text = this.compareDealTime(item.dateTime) + `开始`;
                        this.backgroundColor = "gray";
                    } else if (
                        this.compare(item.dateTime) == false &&
                        this.compare(`${item.date} ${item.endTime}`)
                    ) {
                        text = "上课";
                        this.backgroundColor = "orange";
                    } else {
                        text = "回看";
                        this.backgroundColor = "orange";
                    }
                }
            }

            return text;
        },
        //比较时间
        compare(time) {
            return moment(time).isAfter(new Date());
        },
        //处理直播课程未开始日期返回
        compareDealTime(time) {
            return moment(time).format("h:mm");
        }
    }
};
